.container {
    background: #333133;
    color: white;
    padding: 8px;
}

.navbar{
    display: flex;
    justify-content: space-between;
    margin: 0px auto;
    max-width: 1200px;
}

.carrito-link{
    cursor: pointer;
    margin: auto 0px;
    text-decoration: none;
}

.imgCarrito{
    height: 50px;
    filter: invert(100%) sepia(68%) saturate(0%) hue-rotate(29deg) brightness(110%) contrast(102%);
}

.imgLogo{
    height: 80px;
}